const Common = {
	// 
    simpleClone: function (p) {
        var c = {};
        for (var i in p) {
            if (p.hasOwnProperty(i)) {
                c[i] = p[i];
            }
        }
        return c;
    },
	deepClone(p, c) {
        var c = c || {};
        for (var i in p) {
            if (typeof p[i] == 'object') {
                c[i] = (p[i].constructor === Array ) ? [] : {};
                deepClone(p[i], c[i]);
            } else {
                if (p.hasOwnProperty(i)) {
                    c[i] = p[i];
                }
            }
        }
        return c;
    },
	/**
	 * date  time  yyyy-MMM-dd HH:mm:ss*
	 * */
    formatDateConcat(ymd,hms){
    		var reg = /\d+/;
		if(!reg.test(ymd) || !reg.test(hms)) {
			return '';
		}
		ymd = String(ymd), hms = String(hms);
		var y = ymd.substring(0, 4),
			M = ymd.substring(4, 6),
			d = ymd.substring(6, 8);
		var H = hms.substring(0, 2),
			m = hms.substring(2, 4),
			s = hms.substring(4, 6);
		return y + "-" + M + "-" + d + " " + H + ":" + m + ":" + s;	
    },
    /**
     *  *
     * */
    /**
     *  *
     * */
    formatDateToNumber(date){
	    	var _date = new Date(date);
	    	if(!date || !_date.getTime()) {
	    		return "";
	    	}
	    	if(typeof date == "string") {
	    		return date.match(/\d/g).join("");
	    	}
	    	var y = _date.getFullYear(),
	    		M = _date.getMonth() + 1,
	    		d = _date.getDate(),
	    		H = _date.getHours(),
	    		m = _date.getMinutes(),
	    		s = _date.getSeconds();
	    	M = M > 9 ? M : ("0" + M);
	    	d = d > 9 ? d : ("0" + d);
	    	H = H > 9 ? H : ("0" + H);
	    	m = m > 9 ? m : ("0" + m);
	    	s = s > 9 ? s : ("0" + s);
	    	return y + M + d + H + m + s;
    },
    /**
     *  *
     * */
    getYMDforNowNull(date,type){
    		var _date = new Date(date);
		if(!date || !_date.getTime()){return null;}
		var y = _date.getFullYear(),
			M = _date.getMonth()+1,
			d = _date.getDate(),
			H = _date.getHours(),
			m = _date.getMinutes(),
			s = _date.getSeconds();
			M = M > 9 ? M : ("0"+M);
			d = d > 9 ? d : ("0"+d);
			H = H > 9 ? H : ("0"+H);
			m = m > 9 ? m : ("0"+m);
			s = s > 9 ? s : ("0"+s);
		return type?(H+""+m+""+s):(y+""+M+""+d);
    },
    /**
     *  *
     * */
    getYMDforNow(date,type){
    		var _date = new Date(date);
		if(!date || !_date.getTime()){return "";}
		var y = _date.getFullYear(),
			M = _date.getMonth()+1,
			d = _date.getDate(),
			H = _date.getHours(),
			m = _date.getMinutes(),
			s = _date.getSeconds();
			M = M > 9 ? M : ("0"+M);
			d = d > 9 ? d : ("0"+d);
			H = H > 9 ? H : ("0"+H);
			m = m > 9 ? m : ("0"+m);
			s = s > 9 ? s : ("0"+s);
		return type?(H+m+s):(y+M+d);
    },
    /**
     *  *
     * */
    getHMSforNow(){
    		var date = new Date(),
    			h = date.getHours(),
    			m = date.getMinutes(),
    			s = date.getSeconds();
    		h = h > 9 ? h : ("0"+h);
    		m = m > 9 ? m : ("0"+m);
    		s = s > 9 ? s : ("0"+s);
    		return h+":"+m+":"+s;
    },
    /**
     * 13yyyy-MM-dd HH:mm:ss *
     * */
    time2DateFormat(t){
    		var date = new Date(t||""),
    			y = date.getFullYear(),
    			M = date.getMonth()+1,
    			d = date.getDate(),
    			h = date.getHours(),
    			m = date.getMinutes(),
    			s = date.getSeconds();
    		M = M > 9 ? M : ("0"+M);
    		d = d > 9 ? d : ("0"+d);
    		h = h > 9 ? h : ("0"+h);
    		m = m > 9 ? m : ("0"+m);
    		s = s > 9 ? s : ("0"+s);
    		return y+"-"+M+"-"+d+" "+h+":"+m+":"+s;
    },
    /**
     * ajax  *
     * */
    filterParamByEmptyValue(param){
    		var obj = {};
        	for(var i in param){
        		if(param[i] || param[i]===false){
        			obj[i] = param[i];
        		}
        	}
        	return obj;
    },
    /**
     * confirm *
     * */
    confirm(title,handleSure,handleCancel){
    		this.$confirm(title, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning'
		}).then(() => {
			if(typeof handleSure =="function")
				handleSure();
		}).catch(() => {       
			if(typeof handleCancel =="function")
				handleCancel();
		});
	},
	//
	compressImg(img, rate, fileType){
		var cvs = document.createElement('canvas');
		var width=img.width;
		var height = img.height;
		var dic=height/width;
		var width_cvs=width>750?750:width;
		cvs.width = width_cvs;  //600px
		cvs.height = width_cvs * dic;
		var cxt=cvs.getContext("2d");
		cxt.fillStyle="#FFF";
		cxt.fillRect(0,0,width_cvs,width_cvs*dic);   
		cxt.drawImage(img,0,0,width_cvs,width_cvs*dic);
		return cvs.toDataURL(fileType, rate);
	},
    batchHttp(){
        
    }
    
};
export default Common;



